*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  overflow: hidden;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.octo-arm {
  transform-origin: 130px 106px;
}

.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}

.github-corner {
  position: fixed;
  z-index: 1;
  right: 0;
}

.github-corner svg {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  color: #fff;
  fill: #151513;
}

#canvas {
  position: fixed;
  top: 0;
  left: 0;
  outline: none;
}
